import React, { Component } from 'react';
import Menu from '../../src';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;



class Demo4 extends Component {

    handleClick = (e) => {
        console.log('click', e);
    }
    render() {
        return (
            <div>
                <input style={{'marginBottom':'10px'}} placeholder=""/>
                <Menu onClick={this.handleClick} keyboard={true} style={{ width: 240 }} mode="vertical">
                <SubMenu key="sub1" title={<span><span> 1</span></span>}>
                    <MenuItemGroup title="Item 1">
                        <Menu.Item key="1"> 1</Menu.Item>
                        <Menu.Item key="2"> 2</Menu.Item>
                    </MenuItemGroup>
                    <MenuItemGroup title="Iteom 2">
                        <Menu.Item key="3"> 3</Menu.Item>
                        <Menu.Item key="4"> 4</Menu.Item>
                    </MenuItemGroup>
                </SubMenu>
                <SubMenu key="sub2" title={<span><span> 2</span></span>}>
                    <Menu.Item key="5"> 5</Menu.Item>
                    <Menu.Item key="6"> 6</Menu.Item>
                    <SubMenu key="sub3" title="Submenu">
                        <Menu.Item key="7"> 7</Menu.Item>
                        <Menu.Item key="8"> 8</Menu.Item>
                    </SubMenu>
                </SubMenu>
                <SubMenu key="sub4" title={<span><span> 3</span></span>}>
                    <Menu.Item key="9"> 9</Menu.Item>
                    <Menu.Item key="10"> 10</Menu.Item>
                    <Menu.Item key="11"> 11</Menu.Item>
                    <Menu.Item key="12"> 12</Menu.Item>
                </SubMenu>
            </Menu>
            </div>
            
        )
    }
}

export default Demo4;


