/**
 * @title Menu
 * @description 
 */

import React, { Component } from 'react';
import Menu from '../../src';

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;


class Demo1 extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            current: 'mail'
        }
    }

    handleClick = (e) => {
        this.setState({
            current: e.key,
        });
    }

    render() {
        return (
            <Menu onClick={this.handleClick}
                  selectedKeys={[this.state.current]}
                  mode="horizontal"
                >
                <Menu.Item key="mail" attribute={{'type': 'mail'}}>
                     1
                </Menu.Item>
                <Menu.Item key="app" disabled>
                     2
                </Menu.Item>
                <SubMenu title={<span> 1 - </span>}>
                    <MenuItemGroup title=" 1">
                        <Menu.Item key="setting:1"> 1</Menu.Item>
                        <Menu.Item key="setting:2"> 2</Menu.Item>
                    </MenuItemGroup>
                    <MenuItemGroup title=" 2">
                        <Menu.Item key="setting:3"> 3</Menu.Item>
                        <Menu.Item key="setting:4"> 4</Menu.Item>
                    </MenuItemGroup>
                </SubMenu>
            </Menu>
        )
    }
}

export default Demo1;


